// POST /api/currency/convert - ØªØ­ÙˆÙŠÙ„ Ø§Ù„Ø¹Ù…Ù„Ø©
export const convertCurrency: RequestHandler = async (req, res) => {
  try {
    const { amount, from = 'EGP', to } = req.body;
    
    if (!amount || !to) {
      return res.status(400).json({ 
        error: 'amount and to currency are required' 
      });
    }
    
    // TODO: Ø§Ø³ØªØ®Ø¯Ø§Ù… Ø£Ø³Ø¹Ø§Ø± Ø­Ù‚ÙŠÙ‚ÙŠØ©
    const rates: Record<string, number> = {
      'EGP_USD': 0.032,
      'EGP_EUR': 0.029,
      'EGP_SAR': 0.12
    };
    
    const rate = rates[`${from}_${to}`] || 1;
    const converted = amount * rate;
    
    res.json({
      from,
      to,
      amount,
      converted,
      rate
    });
  } catch (error) {
    console.error('Error converting currency:', error);
    res.status(500).json({ error: 'Failed to convert currency' });
  }
};
